*, *::before, ::after{
  box-sizing: border-box;
}

body{
  margin: 0%;
  background: linear-gradient(to right, grey);
}

.calculator-grid{
  display: grid;
  margin-top: 12rem;
  grid-template-columns: repeat(4, 6rem);
  grid-template-rows: repeat(7rem, auto) repeat(5,6rem);
  margin-left: 450px;
  
}



.calculator-grid>button{
  cursor: pointer;
  font-size: 2rem;
  border: 1px solid wheat;
  outline:none;
  background-color: rgba(0, 0, 255, 0.384);
}

.calculator-grid>button:hover,
.calculator-grid>button:focus{
  background-color: blue;
}

.spain-two{
  grid-column: span 2;
}

.output{
  grid-column: 1/ -1;
  background-color: rgb(98, 98, 109);
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  padding: .95rem;
  word-wrap: break-word;
  word-break: break-all;
}

.output .previous-operand{
  color: white;
  font-size: 1.5rem;
}

.output .current-operand{
color: rgba(255,255,255,75);
font-size: 2.5 rem;  
}


@media (max-width:768px) {
  .calculator-grid{
    margin-left: 4rem;
  }
  .spain-two{
    grid-column: span 4;
  }
 
  
}
@media (max-width:998px) {
  .calculator-grid{
    margin-left: 10rem;
  }
  .calculator-grid>button{
    font-size: .75rem;
  }
  
}

@media only screen and (-webkit-min-device-pixel-ratio: 3) {
  .calculator-grid{
    margin-right: auto;
  }
  .calculator-grid>button{
    font-size: .75rem;
  }
  
}